import { useState, useMemo } from 'react';

const styles = {
	childBox: {
		padding: '8px',
		border: '1px solid red',
		margin: '8px',
	}
};

const ChildA1 = () => {
	console.log('render', 'ChildA1', 'useMemo outer', 1);
	return useMemo(() => {
        console.log('render', 'ChildA1', 'useMemo inter', 2);
        return (<div style={styles.childBox}>ChildA1 区域</div>)
    }, []);
}
const ChildA2 = (props) => {
	console.log('render', 'ChildA2', 'useMemo outer', 1);
	return useMemo(() => {
        console.log('render', 'ChildA2', 'useMemo inter', 2, '依赖prpps.countA,所以会重新渲染');
        return (<div style={styles.childBox}>ChildA2 区域:{props.countA}</div>)
    }, [props.countA]);
}
const ChildA3 = () => {
	console.log('render', 'ChildA3');
	return (<div style={styles.childBox}>ChildA3 区域</div>)
}

const Demo = () => {
	const [countA, setCountA] = useState(11);
	
	const fn = () => {
		console.log('点击事件', '父组件 Home2 变量 countA 被重新赋值', '所有子组件全部重新渲染');
		setCountA(countA + 1);
	}
	console.log('render', 'Home2', '--------------------');
	return (
		<div style={styles.childBox}>
			<div>Home2区域</div>
			<button onClick={fn}>countA:{countA}</button>
			<ChildA1></ChildA1>
			<ChildA2 countA={countA}></ChildA2>
			<ChildA3></ChildA3>
		</div>
	);
};

export default Demo;