import { useState } from 'react';

const styles = {
	childBox: {
		padding: '8px',
		border: '1px solid red',
		margin: '8px',
	}
};

const ChildA1 = () => {
	console.log('render', 'ChildA1');
	return (<div style={styles.childBox}>ChildA1 区域</div>)
}
const ChildA2 = () => {
	console.log('render', 'ChildA2');
	return (<div style={styles.childBox}>ChildA2 区域</div>)
}
const ChildA3 = () => {
	console.log('render', 'ChildA3');
	return (<div style={styles.childBox}>ChildA3 区域</div>)
}

const Demo = () => {
	const [countA, setCountA] = useState(11);
	
	const fn = () => {
		console.log('点击事件', '父组件 Home1 变量 countA 被重新赋值', '所有子组件全部重新渲染');
		setCountA(countA + 1);
	}
	console.log('render', 'Home1', '--------------------');
	return (
		<div style={styles.childBox}>
			<div>Home1区域</div>
			<button onClick={fn}>countA:{countA}</button>
			<ChildA1></ChildA1>
			<ChildA2></ChildA2>
			<ChildA3></ChildA3>
		</div>
	);
};

export default Demo;